import { Component, OnInit } from '@angular/core';
import { ActivatedRoute } from '@angular/router';

import { IPronounce } from '../pronounce.model';
import { DataUtils } from 'app/core/util/data-util.service';
import {DomSanitizer} from '@angular/platform-browser';


@Component({
  selector: 'jhi-pronounce-detail',
  templateUrl: './pronounce-detail.component.html',
})
export class PronounceDetailComponent implements OnInit {
  pronounce: IPronounce | null = null;
  audioPath : any | null = null ;
  data : string | null = "data:audio/mpeg;base64,";
  img : any | null = "data:image/svg+xml;base64,"


  constructor(protected dataUtils: DataUtils, protected activatedRoute: ActivatedRoute, private _sanitizer:DomSanitizer) {}

  ngOnInit(): void {
    this.activatedRoute.data.subscribe(({ pronounce }) => {
      this.pronounce = pronounce;
      if (pronounce?.pronunciation){
        this.audioPath = this._sanitizer.bypassSecurityTrustUrl( String(this.data));
        this.img = this._sanitizer.bypassSecurityTrustUrl( String(this.img));

      }


    });
  }

  byteSize(base64String: string): string {
    return this.dataUtils.byteSize(base64String);
  }

  openFile(base64String: string, contentType: string | null | undefined): void {
    this.dataUtils.openFile(base64String, contentType);
  }

  previousState(): void {
    window.history.back();
  }

  /*sanitize(url:string) : SafeUrl{
      //return this.sanitizer.bypassSecurityTrustUrl("data:audio/mpeg;base64," + url);

     return  this._sanitizer.bypassSecurityTrustResourceUrl('data:audio/mpeg;base64,'
                       + url);
  }*/

}
